<template>
  <div>
    <div class="ithit-grid-toolbar row">
      <div class="col pl-1">
        <BaseToolbarButton
          class="btn-create-folder"
          title="Create Folder"
          @click-event="showCreateFolderModal"
        >
          <span class="d-none d-lg-inline text-nowrap">Create Folder</span>
        </BaseToolbarButton>
      </div>
      <div class="col-auto px-0 px-md-3">
        <UploadButton />
      </div>
      <div class="col-auto px-0 px-md-3">
        <BaseToolbarButton
          class="btn-rename-item"
          title="Rename Item"
          :disabled="!selectedItems.length || selectedItems.length > 1"
          @click-event="showRenameItemModal"
        >
        </BaseToolbarButton>
      </div>
      <div class="col-auto px-0 px-md-3">
        <BaseToolbarButton
          class="btn-copy-items"
          title="Copy"
          :disabled="!selectedItems.length"
          @click-event="storeSelectedItems(storedType.Copy)"
        /><BaseToolbarButton
          class="btn-cut-items"
          title="Cut"
          :disabled="!selectedItems.length"
          @click-event="storeSelectedItems(storedType.Cut)"
        /><BaseToolbarButton
          class="btn-paste-items"
          title="Paste"
          :disabled="!storedItems.length"
          @click-event="pasteStoredItems"
        />
      </div>
      <div class="col-auto px-0 px-md-3">
        <BaseToolbarButton
          class="btn-reload-items"
          title="Reload"
          @click-event="reload"
        />
      </div>
      <div class="col-auto px-1 px-lg-3">
        <BaseToolbarButton
          class="btn-print-items"
          title="Print"
          :disabled="!selectedItems.length"
          @click-event="showPrintItemsModal"
        >
          <span class="d-none d-lg-inline">Print</span>
        </BaseToolbarButton>
        <BaseToolbarButton
          class="btn-delete-items"
          title="Delete"
          :disabled="!selectedItems.length"
          @click-event="showDeleteItemsModal"
        >
          <span class="d-none d-lg-inline">Delete</span>
        </BaseToolbarButton>
      </div>
    </div>
    <div v-if="createFolderModalDisplayed">
      <CreateFolderModal @close-modal="hideCreateFolderModal" />
    </div>
    <div v-if="deleteItemsModalDisplayed">
      <DefaultModal
        message="Are you sure want to delete selected items?"
        @close-modal="hideDeleteItemsModal"
        @submit-modal="deleteSelectedItems"
      />
    </div>
    <div v-if="renameItemModalDisplayed">
      <RenameItemModal
        message="Are you sure want to delete selected items?"
        @close-modal="hideRenameItemModal"
      />
    </div>
    <div v-if="printItemsModalDisplayed">
      <DefaultModal
        message="Are you sure want to print selected items?"
        @close-modal="hideRenameItemModal"
        @submit-modal="printSelectedItems"
      />
    </div>
  </div>
</template>
<script>
import CreateFolderModal from "@/components/modals/CreateFolderModal.vue";
import BaseToolbarButton from "@/components/toolbar/BaseToolbarButton.vue";
import DefaultModal from "@/components/modals/DefaultModal.vue";
import RenameItemModal from "@/components/modals/RenameItemModal.vue";
import UploadButton from "@/components/toolbar/UploadButton.vue";
import { StoredType } from "@/models/StoredType.js";
export default {
  components: {
    CreateFolderModal,
    BaseToolbarButton,
    DefaultModal,
    RenameItemModal,
    UploadButton
  },
  data() {
    return {
      createFolderModalDisplayed: false,
      deleteItemsModalDisplayed: false,
      printItemsModalDisplayed: false,
      renameItemModalDisplayed: false,
      storedType: StoredType
    };
  },
  computed: {
    selectedItems() {
      return this.$store.state.grid.SelectedItems;
    },
    storedItems() {
      return this.$store.state.grid.StoredItems;
    }
  },
  methods: {
    showCreateFolderModal() {
      this.createFolderModalDisplayed = true;
    },
    hideCreateFolderModal() {
      this.createFolderModalDisplayed = false;
    },
    showRenameItemModal() {
      this.renameItemModalDisplayed = true;
    },
    hideRenameItemModal() {
      this.renameItemModalDisplayed = false;
    },
    showPrintItemsModal() {
      this.printItemsModalDisplayed = true;
    },
    hidePrintItemsModal() {
      this.printItemsModalDisplayed = false;
    },
    showDeleteItemsModal() {
      this.deleteItemsModalDisplayed = true;
    },
    hideDeleteItemsModal() {
      this.deleteItemsModalDisplayed = false;
    },
    storeSelectedItems(type) {
      this.$store.commit("storeSelectedItems", type);
      this.hideDeleteItemsModal();
    },
    deleteSelectedItems() {
      this.$store.commit("deleteSelectedItems");
      this.hidePrintItemsModal();
    },
    printSelectedItems() {
      this.$store.commit("printDocs");
    },
    pasteStoredItems() {
      this.$store.commit("pasteStoredItems");
    },
    reload() {
      this.$store.commit("updateGrid");
    }
  }
};
</script>
