<template>
  <div class="ithit-search-container">
    <div class="twitter-typeahead">
      <input
        v-model="query"
        class="form-control"
        :disabled="isDisabled"
        :placeholder="isDisabled ? 'The server does not support search' : ''"
        @focus="isFocusInput = true"
        @blur="isFocusInput = false"
        @keyup.enter="setSearchQuery()"
      />
      <div
        v-if="showMenu"
        class="tt-menu"
        style="position: absolute;top: 100%;left: 0px;z-index: 100;"
        @mouseover="isMouseOverMenu = true"
        @mouseout="isMouseOverMenu = false"
      >
        <div class="tt-dataset tt-dataset-states">
          <div
            v-for="(item, index) in items"
            :key="index"
            class="tt-suggestion tt-selectable"
            @click.prevent="setItem(item)"
          >
            {{ item.DisplayName }}
            <SnippetSearch :item="item" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import WebDavService from "@/services/WebDavService.js";
import UrlResolveService from "@/services/UrlResolveService.js";
import SnippetSearch from "@/components/SnippetSearch.vue";
import { WebDavError } from "@/models/WebDavError.js";
import ErrorMessages from "@/models/ErrorMessages.js";
import _ from "underscore";
export default {
  components: {
    SnippetSearch
  },
  data() {
    return {
      query: "",
      items: [],
      isFocusInput: false,
      isMouseOverMenu: false,
      maxShowItems: 6
    };
  },
  computed: {
    showMenu() {
      return (this.items.length && this.isFocusInput) || this.isMouseOverMenu;
    },
    isDisabled() {
      return this.loading
        ? true
        : !(
            this.$store.state.grid.OptionsInfo.Features &
            window.ITHit.WebDAV.Client.Features.Dasl
          );
    },
    loading() {
      return this.$store.state.grid.OptionsInfoLoading;
    }
  },
  watch: {
    query: _.debounce(function(q) {
      this.getItems(q);
    }, 500)
  },
  created() {
    this.query = this.$store.state.grid.SearchQuery;
  },
  methods: {
    /**
     * Gets items
     * @param {String} query Search query
     */
    getItems(query) {
      if (this.$store.state.grid.CurrentPage != 1) {
        this.$router.push(UrlResolveService.setPage(1));
      }

      if (query) {
        WebDavService.getItemsByQuery(
          this.$store.state.grid.CurrentFolder,
          1,
          this.maxShowItems,
          query
        )
          .then(resp => {
            this.items = resp.Page;
          })
          .catch(err => {
            this.$store.commit(
              "setError",
              new WebDavError(ErrorMessages.SearchErrorMessage, err.Error)
            );
          });
      } else {
        this.items = [];
      }
    },
    /**
     * Sets selected item to store
     * @param {HierarchyItem} item Search query
     */
    setItem(item) {
      this.query = item.DisplayName;
      this.$store.commit("setSearchItem", item);
      this.isMouseOverMenu = false;
    },
    /**
     * Sets search query to store
     */
    setSearchQuery() {
      if (this.query) {
        this.$router.push(UrlResolveService.setSearch(this.query));
      }
    }
  }
};
</script>
