<template>
  <div>
    <div
      id="CreateFolderModal"
      class="modal show"
      style="padding-right: 17px; display: block;"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <form @submit.prevent="validFormData">
            <div class="modal-header">
              <h5 class="modal-title">
                Create Folder
              </h5>
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                aria-label="Close"
                @click.prevent="closeModal"
              >
                <span aria-hidden="true">×</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="form-group">
                <input
                  v-model="folderName"
                  v-focus
                  type="text"
                  class="form-control"
                  placeholder="Folder Name"
                />
                <div v-if="errorMessage" class="alert alert-danger">
                  {{ errorMessage }}
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="submit" class="btn btn-primary btn-submit">
                OK
              </button>
              <button
                type="button"
                class="btn btn-default"
                data-dismiss="modal"
                @click.prevent="closeModal"
              >
                Cancel
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
    <div class="modal-backdrop show" />
  </div>
</template>
<script>
import WebDavService from "@/services/WebDavService.js";
import CommonService from "@/services/CommonService.js";
import { Escapable } from "@/mixins/Escapable.js";
export default {
  mixins: [Escapable],
  emits: ["close-modal"],
  data() {
    return {
      folderName: "",
      errorMessage: ""
    };
  },
  methods: {
    /**
     * Close modal event
     */
    closeModal() {
      this.$emit("close-modal");
    },
    /**
     * Validate data
     */
    validFormData() {
      if (this.folderName != null && this.folderName.match(/^ *$/) === null) {
        this.errorMessage = CommonService.Validators.ValidateName(
          this.folderName
        );
        if (!this.errorMessage) {
          this.createFolder();
        }
      } else {
        this.errorMessage = "Name is required!";
      }
    },
    /**
     * Create folder
     */
    createFolder() {
      WebDavService.createFolder(
        this.$store.state.grid.CurrentFolder,
        this.folderName
      )
        .then(() => {
          this.$store.commit("updateGrid");
          this.$emit("close-modal");
        })
        .catch(resp => {
          if (
            resp.Error instanceof
            window.ITHit.WebDAV.Client.Exceptions.MethodNotAllowedException
          ) {
            this.errorMessage = resp.Error.Error.Description
              ? resp.Error.Error.Description
              : "Folder already exists.";
          }
        });
    },
    escapeHandler() {
      this.closeModal();
    }
  }
};
</script>
