<template>
  <div
    id="ithit-dropzone"
    :class="{ dropzone: isDragging }"
    @dragenter="dragEnterHandler"
    @dragleave="dragLeaveHandler"
    @drop="dropHandler"
  >
    <div v-if="!loading">
      <Breadcrumb :s-url="$route.path" />
      <Search />
      <Toolbar />
      <Uploader />
      <GridContainer />
      <Pagination />
    </div>
    <div>
      <ErrorModal />
      <DownloadProtocolModal />
      <RewriteModal />
    </div>
    <input
      id="ithit-hidden-input"
      ref="ithitHiddenInput"
      class="d-none"
      type="file"
      multiple
    />
  </div>
</template>
<script>
import Breadcrumb from "@/components/Breadcrumb.vue";
import GridContainer from "@/components/grid/GridContainer.vue";
import Pagination from "@/components/Pagination.vue";
import Search from "@/components/Search.vue";
import Toolbar from "@/components/toolbar/Toolbar.vue";
import ErrorModal from "@/components/modals/ErrorModal.vue";
import DownloadProtocolModal from "@/components/modals/DownloadProtocolModal.vue";
import Uploader from "@/components/uploader/Uploader.vue";
import UploadService from "@/services/UploadService.js";
import RewriteModal from "@/components/modals/RewriteModal.vue";
export default {
  components: {
    Breadcrumb,
    GridContainer,
    Pagination,
    Search,
    Toolbar,
    ErrorModal,
    DownloadProtocolModal,
    Uploader,
    RewriteModal
  },
  data() {
    return {
      isDragging: false,
      dropCounter: 0
    };
  },
  computed: {
    loading() {
      return this.$store.state.grid.Loading;
    }
  },
  mounted() {
    this.$nextTick(() => {
      UploadService.init();
    });
  },
  methods: {
    dragEnterHandler() {
      this.dropCounter++;
      this.isDragging = true;
    },
    dragLeaveHandler() {
      this.dropCounter--;
      if (this.dropCounter <= 0) {
        this.dropCounter = 0;
        this.isDragging = false;
      }
    },
    dropHandler() {
      this.dropCounter = 0;
      this.isDragging = false;
    }
  }
};
</script>
