<template>
  <div>
    <button
      v-if="item.IsFolder"
      class="btn btn-primary btn-sm btn-labeled"
      type="button"
      @click="openFolderInOsFileManager()"
    >
      <span class="btn-label"><i class="icon-open-folder"/></span>
      <span class="d-none d-lg-inline-block">Browse</span>
    </button>
    <div v-else class="btn-group">
      <button
        type="button"
        class="btn btn-primary btn-sm btn-labeled btn-default-edit"
        @click.prevent="editDocument()"
      >
        <span class="btn-label"><i class="icon-edit"/></span>
        <span class="d-none d-lg-inline-block btn-edit-label">Edit</span>
      </button>
      <button
        type="button"
        class="btn btn-primary dropdown-toggle dropdown-toggle-split btn-sm"
        @click.prevent="isShowMenu = !isShowMenu"
      >
        <span class="sr-only">Toggle Dropdown</span>
      </button>
      <v-popover
        placement="left-end"
        offset="1"
        :open="isShowMenu"
        popover-class="mb-3"
        @apply-hide="isShowMenu = false"
      >
        <template slot="popover">
          <div class="dropdown-menu actions show">
            <a
              class="dropdown-item"
              href="javascript:void()"
              title="Edit document with desktop associated application."
              @click.prevent="editDocument()"
              ><i class="icon-edit-associated" />Edit with Associated Desktop
              Application
            </a>
            <div class="dropdown-divider" />
            <a
              class="dropdown-item desktop-app"
              href="javascript:void()"
              title="Select application to open this file with."
              @click.prevent="openDocumentWith()"
              >Select Desktop Application
            </a>
          </div>
        </template>
      </v-popover>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import VTooltip from "v-tooltip";
import webDavSettings from "@/WebDavSettings.js";
import CommonService from "@/services/CommonService.js";
import WebDavService from "@/services/WebDavService.js";

Vue.use(VTooltip);
export default {
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      isShowMenu: false
    };
  },
  methods: {
    /**
     * Opens folder in operating system file manager
     */
    openFolderInOsFileManager() {
      WebDavService.DocManager.OpenFolderInOsFileManager(
        this.item.Href,
        CommonService.Formatters.RootUrl(),
        this._protocolInstallMessage,
        null,
        webDavSettings.EditDocAuth.SearchIn,
        webDavSettings.EditDocAuth.CookieNames,
        webDavSettings.EditDocAuth.LoginUrl
      );
    },
    /**
     * Opens document for editing
     */
    editDocument() {
      if (
        webDavSettings.EditDocAuth.Authentication.toLowerCase() == "cookies"
      ) {
        WebDavService.DocManager.DavProtocolEditDocument(
          this.item.Href,
          CommonService.Formatters.RootUrl(),
          this._protocolInstallMessage,
          null,
          webDavSettings.EditDocAuth.SearchIn,
          webDavSettings.EditDocAuth.CookieNames,
          webDavSettings.EditDocAuth.LoginUrl
        );
      } else {
        WebDavService.DocManager.EditDocument(
          this.item.Href,
          CommonService.Formatters.RootUrl(),
          this._protocolInstallMessage
        );
      }
    },
    /**
     * Selects application and opens document for editing
     */
    openDocumentWith() {
      WebDavService.DocManager.DavProtocolEditDocument(
        this.item.Href,
        CommonService.Formatters.RootUrl(),
        this._protocolInstallMessage,
        null,
        webDavSettings.EditDocAuth.SearchIn,
        webDavSettings.EditDocAuth.CookieNames,
        webDavSettings.EditDocAuth.LoginUrl,
        "OpenWith"
      );
    },
    _protocolInstallMessage() {
      this.$store.commit("showProtocolModal");
    }
  }
};
</script>
