<template>
  <div v-if="rewriteItemsData">
    <div
      class="modal show"
      tabindex="-1"
      role="dialog"
      style="padding-right: 17px; display: block;"
    >
      <div class="modal-dialog modal-sm" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              Confirm
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
              @click.prevent="closeModal"
            >
              <span aria-hidden="true">×</span>
            </button>
          </div>
          <div class="modal-body">
            <p class="message">
              The following item(s) exist on the server:
            </p>
            <p class="message" v-html="rewriteItemsData.ItemsList"></p>
            <p class="message">
              Overwrite?
            </p>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-primary btn-ok"
              @click.prevent="submitModal"
            >
              Yes
            </button>
            <button
              type="button"
              class="btn btn-primary btn-default"
              @click.prevent="unsubmitModal"
            >
              No
            </button>
            <button
              type="button"
              class="btn btn-default"
              data-dismiss="modal"
              @click.prevent="closeModal"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-backdrop show" />
  </div>
</template>
<script>
export default {
  computed: {
    rewriteItemsData() {
      return this.$store.state.uploader.RewriteItemsData;
    }
  },
  methods: {
    submitModal() {
      this.rewriteItemsData.OnOverwrite();
      this.closeModal();
    },
    unsubmitModal() {
      this.rewriteItemsData.OnSkipExists();
      this.closeModal();
    },
    closeModal() {
      this.$store.commit("SetRewriteItemsData", null);
    }
  }
};
</script>
