export class WebDavProtocol {
  CurrentOs = null;
  CurrentBrowser = null;
  OtherOs = [];
  OtherBrowsers = [];
  constructor(CurrentOs, CurrentBrowser, OtherOs, OtherBrowsers) {
    (this.CurrentOs = CurrentOs),
      (this.CurrentBrowser = CurrentBrowser),
      (this.OtherOs = OtherOs),
      (this.OtherBrowsers = OtherBrowsers);
  }
}
