<template>
  <div>
    <div
      id="RenameItemModal"
      class="modal show"
      style="padding-right: 17px; display: block;"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <form @submit.prevent="validFormData">
            <div class="modal-header">
              <h5 class="modal-title">
                Rename Item
              </h5>
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                aria-label="Close"
                @click.prevent="closeModal"
              >
                <span aria-hidden="true">×</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="form-group">
                <input
                  v-model="newItemName"
                  v-focus
                  type="text"
                  class="form-control"
                  placeholder="Item Name"
                />
                <div v-if="errorMessage" class="alert alert-danger">
                  {{ errorMessage }}
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="submit" class="btn btn-primary btn-submit">
                OK
              </button>
              <button
                type="button"
                class="btn btn-default"
                data-dismiss="modal"
                @click.prevent="closeModal"
              >
                Cancel
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
    <div class="modal-backdrop show" />
  </div>
</template>
<script>
import CommonService from "@/services/CommonService.js";
import { Escapable } from "@/mixins/Escapable.js";
export default {
  mixins: [Escapable],
  emits: ["close-modal"],
  data() {
    return {
      newItemName: "",
      oldItemName: "",
      errorMessage: ""
    };
  },
  computed: {
    selectedItems() {
      return this.$store.state.grid.SelectedItems;
    }
  },
  created() {
    if (this.selectedItems.length) {
      this.oldItemName = this.selectedItems[0].DisplayName;
      this.newItemName = this.selectedItems[0].DisplayName;
    }
  },
  methods: {
    /**
     * Close modal event
     */
    closeModal() {
      this.$emit("close-modal");
    },
    /**
     * validate data
     */
    validFormData() {
      if (this.oldItemName == this.newItemName) {
        this.$emit("close-modal");
      } else if (
        this.newItemName != null &&
        this.newItemName.match(/^ *$/) === null
      ) {
        this.errorMessage = CommonService.Validators.ValidateName(
          this.newItemName
        );
        if (!this.errorMessage) {
          this.renameItem();
        }
      } else {
        this.errorMessage = "Name is required!";
      }
    },
    /**
     * Rename Item
     */
    renameItem() {
      this.$emit("close-modal");
      this.$store.commit("renameSelectedItem", this.newItemName);
    },
    escapeHandler() {
      this.closeModal();
    }
  }
};
</script>
