<template>
  <div class="file-icon" :class="'file-' + fileExtension.toLowerCase()">
    <span v-if="fileExtension.length < 5" class="file-extension">{{
      fileExtension.toUpperCase()
    }}</span>
  </div>
</template>
<script>
export default {
  props: {
    fileExtension: {
      type: String,
      default: ""
    }
  }
};
</script>
