<template>
  <div v-if="countPages && countPages > 1" class="row">
    <div class="col-12 align-items-end">
      <nav aria-label="Page navigation">
        <ul
          class="pagination flex-wrap justify-content-end ithit-pagination-container"
        >
          <li class="page-item" :class="currentPage == 1 ? 'disabled' : ''">
            <a class="page-link" @click.prevent="goToPage(1)">&lt;&lt;</a>
          </li>
          <li
            v-for="item in paginationItems"
            :key="item"
            class="page-item"
            :class="currentPage == item ? 'active' : ''"
          >
            <a
              :class="currentPage == item ? 'disabled' : ''"
              class="page-link"
              @click.prevent="goToPage(item)"
              >{{ item }}</a
            >
          </li>
          <li
            class="page-item"
            :class="currentPage == countPages ? 'disabled' : ''"
          >
            <a class="page-link" @click.prevent="goToPage(countPages)"
              >&gt;&gt;</a
            >
          </li>
        </ul>
      </nav>
    </div>
  </div>
</template>
<script>
import UrlResolveService from "@/services/UrlResolveService.js";

export default {
  computed: {
    countPages() {
      return this.$store.state.grid.CountPages;
    },
    currentPage() {
      return this.$store.state.grid.CurrentPage;
    },
    paginationItems() {
      const pItems = [];
      for (let i = 1; i <= this.countPages; i++) {
        pItems.push(i);
      }
      return pItems;
    }
  },
  methods: {
    /**
     * Gets page by number
     * @param {Number} pageNumber Number of page
     */
    goToPage(pageNumber) {
      this.$router.push(UrlResolveService.setPage(pageNumber));
    }
  }
};
</script>
