<template>
  <div>
    <Breadcrumb :s-url="item.Href.replace(item.Host, '')" />
    <div class="snippet" v-html="getSnippet(item)" />
  </div>
</template>
<script>
import WebDavService from "@/services/WebDavService.js";
import CommonService from "@/services/CommonService.js";
import Breadcrumb from "@/components/Breadcrumb.vue";
export default {
  components: {
    Breadcrumb
  },
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  methods: {
    /**
     * Gets snippet
     */
    getSnippet(item) {
      return CommonService.Formatters.Snippet(
        item.Properties.Find(WebDavService.SnippetPropertyName)
      );
    }
  }
};
</script>
