<template>
  <nav aria-label="breadcrumb">
    <ol class="breadcrumb ithit-breadcrumb-container">
      <li class="breadcrumb-item">
        <router-link to="/">
          <span class="icon-home" />
        </router-link>
      </li>
      <li
        v-for="(item, index) in parts"
        :key="index"
        class="breadcrumb-item"
        :class="index == parts.length - 1 ? 'active' : ''"
      >
        <router-link v-if="index != parts.length - 1" :to="getHref(index)">
          <span>{{ decodeURIComponent(item) }}</span>
        </router-link>
        <span v-else>{{ decodeURIComponent(item) }}</span>
      </li>
    </ol>
  </nav>
</template>
<script>
export default {
  name: "Breadcrumb",
  props: {
    sUrl: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      parts: []
    };
  },
  created() {
    this.getParts(this.sUrl);
  },
  methods: {
    /**
     * Gets Breadcrumb item href
     */
    getHref(index) {
      return "/" + this.parts.slice(0, index + 1).join("/") + "/";
    },
    /**
     * Gets Breadcrumb parts
     */
    getParts(sUrl) {
      this.parts = sUrl
        .split("/")
        .slice()
        .filter(function(v) {
          return v;
        });
    }
  }
};
</script>
