<template>
  <div
    v-if="!(currentState === 'Completed' || currentState === 'Canceled')"
    class="row uploading-item"
  >
    <div class="col-auto px-0">
      <button
        class="cancel-button float-left"
        :disabled="disabledActions"
        @click.prevent="cancelUploading"
      ></button>
    </div>
    <div class="col-auto px-0">
      <UploadingFileIcon :file-extension="fileExtension" />
    </div>
    <div class="col">
      <div class="row align-items-center">
        <div class="col-auto item-name">{{ fileName }}</div>
        <div class="col-auto item-size">{{ fileSize }}</div>
        <div v-if="retryMessage" class="col-auto text-danger">
          {{ retryMessage }}
        </div>
      </div>
      <div class="row">
        <div class="col">
          <div class="progress">
            <div
              class="progress-bar"
              role="progressbar"
              :style="'width:' + progress + '%'"
              :aria-valuenow="progress"
              aria-valuemin="0"
              aria-valuemax="100"
            ></div>
          </div>
        </div>
      </div>
      <div class="row justify-content-between mt-1">
        <div class="col-auto item-progress">{{ progress }}% done</div>
        <div class="col-auto item-speed">{{ speed }}</div>
      </div>
    </div>
    <div class="col-auto px-0">
      <button
        v-if="!isPaused"
        :disabled="disabledActions"
        class="pause-button float-right"
        @click.prevent="pauseUploading"
      ></button>
      <button
        v-else
        :disabled="disabledActions"
        class="play-button float-right"
        @click.prevent="playUploading"
      ></button>
    </div>
  </div>
</template>
<script>
import CommonService from "@/services/CommonService.js";
import UploadingFileIcon from "@/components/uploader/UploadingFileIcon.vue";
export default {
  components: {
    UploadingFileIcon
  },
  props: {
    uploadItemRow: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      disabledActions: false
    };
  },
  computed: {
    fileName() {
      return this.uploadItemRow.UploadItem.GetName();
    },
    progress() {
      return this.uploadItemRow.UploadItem.GetProgress().Completed;
    },
    speed() {
      return (
        CommonService.Formatters.FileSize(
          this.uploadItemRow.UploadItem.GetProgress().Speed
        ) + "/sec"
      );
    },
    fileSize() {
      return CommonService.Formatters.FileSize(
        this.uploadItemRow.UploadItem.GetProgress().TotalBytes
      );
    },
    fileExtension() {
      return CommonService.Formatters.GetFileExtension(this.fileName);
    },
    isPaused() {
      return this.currentState === "Paused";
    },
    currentState() {
      return this.uploadItemRow.CurrentState;
    },
    retryMessage() {
      return this.uploadItemRow.RetryMessage;
    }
  },
  methods: {
    pauseUploading() {
      this.toggleDisabledActions(true);
      this.uploadItemRow._PauseClickHandler(this.toggleDisabledActions(false));
    },
    playUploading() {
      this.toggleDisabledActions(true);
      this.uploadItemRow._PlayClickHandler(this.toggleDisabledActions(false));
    },
    cancelUploading() {
      this.toggleDisabledActions(true);
      this.uploadItemRow._CancelClickHandler(this.toggleDisabledActions(false));
    },
    toggleDisabledActions(disabled) {
      this.disabledActions = disabled;
    }
  }
};
</script>
