<template>
  <div>
    <div
      class="modal show"
      tabindex="-1"
      role="dialog"
      style="padding-right: 17px; display: block;"
    >
      <div class="modal-dialog modal-sm" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              Confirm
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
              @click.prevent="closeModal"
            >
              <span aria-hidden="true">×</span>
            </button>
          </div>
          <div class="modal-body">
            <p class="message">
              {{ message }}
            </p>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-primary btn-ok"
              @click.prevent="submitModal"
            >
              OK
            </button>
            <button
              type="button"
              class="btn btn-default"
              data-dismiss="modal"
              @click.prevent="closeModal"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-backdrop show" />
  </div>
</template>
<script>
import { Escapable } from "@/mixins/Escapable.js";
export default {
  mixins: [Escapable],
  emits: ["close-modal", "submit-modal"],
  props: {
    message: {
      type: String,
      required: true
    }
  },
  computed: {
    selectedItems() {
      return this.$store.state.grid.SelectedItems;
    }
  },
  methods: {
    /**
     * Close modal
     */
    closeModal() {
      this.$emit("close-modal");
    },
    /**
     * Submit Modal
     */
    submitModal() {
      this.$emit("submit-modal");
      this.$emit("close-modal");
    },
    escapeHandler() {
      this.closeModal();
    }
  }
};
</script>
