<template>
  <div v-if="webDavError != null">
    <div id="ErrorModal" class="modal show d-block" tabindex="-1" role="dialog">
      <div class="modal-dialog modal-md" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              Error
            </h5>
            <button type="button" class="close" @click.prevent="closeModal">
              <span>&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="container-fluid">
              <div class="row">
                <div class="col-md-4">
                  <p>Error message:</p>
                </div>
                <div class="col-md-8">
                  <p class="error-message" v-html="webDavError.ErrorMessage" />
                </div>
              </div>
              <div class="row error-details-row">
                <div class="col-md-12">
                  <p>
                    <button
                      class="btn btn-default"
                      type="button"
                      @click.prevent="isOpenedDetails = !isOpenedDetails"
                    >
                      Error Details
                    </button>
                  </p>
                  <div
                    id="error-details-collapse"
                    class="collapse"
                    :class="{ show: isOpenedDetails }"
                  >
                    <div class="card card-body">
                      <div class="row">
                        <div class="col-md-2">
                          <p>Url:</p>
                        </div>
                        <div class="col-md-10">
                          <p
                            class="error-details-url"
                            v-html="htmlEscape(webDavError.Error.Uri)"
                          />
                        </div>
                      </div>
                      <div class="row">
                        <div class="col-md-4 ">
                          <p>Error message:</p>
                        </div>
                        <div class="col-md-8">
                          <p
                            class="error-details-message"
                            v-html="formatMessage(webDavError.Error.Message)"
                          />
                        </div>
                      </div>
                      <div class="row error-details-row">
                        <div class="col-md-12">
                          <iframe style="width: 100%; border: none" />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-default"
              @click.prevent="closeModal"
            >
              Close
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-backdrop show" />
  </div>
</template>
<script>
import CommonService from "@/services/CommonService.js";
import { Escapable } from "@/mixins/Escapable.js";
export default {
  mixins: [Escapable],
  data() {
    return {
      isOpenedDetails: false
    };
  },
  computed: {
    webDavError() {
      return this.$store.state.grid.Error;
    }
  },
  methods: {
    htmlEscape(sText) {
      return CommonService.Formatters.HtmlEscape(sText);
    },
    formatMessage(sMessage) {
      return this.htmlEscape(sMessage)
        .replace(/\n/g, "<br />\n")
        .replace(/\t/g, "&nbsp;&nbsp;&nbsp;&nbsp;");
    },
    closeModal() {
      this.$store.commit("clearError");
    },
    escapeHandler() {
      this.closeModal();
    }
  }
};
</script>
