<template>
  <th :class="cssClassAscending" @click.prevent="sortItems">
    <slot />
  </th>
</template>
<script>
import UrlResolveService from "@/services/UrlResolveService.js";
export default {
  props: {
    sortColumn: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      sortAscending: false
    };
  },
  computed: {
    selectedSortAscending() {
      return this.$store.state.grid.SortAscending;
    },
    cssClassAscending() {
      return this.$store.state.grid.SortColumn == this.sortColumn
        ? this.selectedSortAscending
          ? "ascending"
          : "descending"
        : "";
    }
  },
  methods: {
    sortItems() {
      this.sortAscending =
        this.$store.state.grid.SortColumn == this.sortColumn
          ? !this.selectedSortAscending
          : !this.sortAscending;
      this.$router.push(
        UrlResolveService.setSortColumn(this.sortColumn, this.sortAscending)
      );
    }
  }
};
</script>
